:root {
    --primary-color: #007bff;
    --secondary-color: #0069d9;
    --border-color: #0062cc;
    --hover-color: #212529;
    --bg-color: #ddd;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.4;
    background: var(--bg-color);
}

p {
    margin: 10px 0;
}

ul, li {
    list-style: none;
}

a {
    text-decoration: none;
    color: #333;
}

